.container-for-ball {
  width: 33vw;
  height: 29.7vh;
  overflow: hidden;
  padding: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 500px;
  /* background: #40f; */
  background: #fff;
}

.container {
  /* width: 400px;
  height: 300px; */
  width: 33vw;
  height: 29.7vh;
  display: flex;
  place-content: center;
  place-items: center;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 30px;
}

.item {
  width: 100px;
  height: 100px;
  background: #ffc200;
  border-radius: 90px;
  cursor: all-scroll;
}

.textWithinCircle {
  margin: 38% 0 0 0;
  color: #555;
}

