@media only screen and (min-width: 2001px) {
	body {
		color: #555;
		/* background-color: #f8f8f8; */
		text-align: center;
		font-family: "Open Sans", sans-serif;
		margin: 0;
		padding: 0;
	}
	
					/* #page {
						width: 700px;	text-align: left;margin-left: auto;margin-right: auto; border: 1px solid #d6d6d6;
						padding: 20px;
					} 
						color: #547ca0; 
					.wrapper-main-2 {
							display: grid; grid-template-rows: 5% 45% 35% 13%; padding: 0; margin:0
					}*/
	
	.wrapper-up-logo {
		background:	#555;
		height: 5%;
		padding-top: 0.1%;
		padding-bottom: 0.1%;
		text-align: left;
		img {
			margin-left: 26.1%;
		}
	}
	
	.wrapper-up {
		background: url("../media/img_hero.jpg") repeat-x;
		background-size: 71%;
		background-position: 21% 36%; 
			h1 {
				// height: 19vh;
				font-size: 3.6em;
				line-height: 0.6em;
				font-weight: bold;
				text-align: left;
				margin-left: 25.8%;
				margin-top: 0;
				margin-bottom: 0;
				padding: 5.2% 0;
		}
	}
	
	.wrapper-middle {
		height: 100%;
		display: grid;
		grid-template-columns: 80% 20%;
		grid-column-gap: 18%;
		margin: 1.5% 17% 3.6% 26%;
		// padding: 1.6% 0 3.3% 0;
		padding: 2.15% 0;
		h1.how-it-started {
			font-size: 2.6em;
			font-weight: bold;
			line-height: 1.2em;
			padding-bottom: 2%;
			color: #ffc200;
			// margin-left: 0%;
			// margin-top: 0;
			margin-bottom: 4%;
		};
		p.thick {
			line-height: 1.5em;
			font-size: 0.9em;
			margin-bottom: 0;
			text-align: center;
		}
		.feedback {
			margin: auto;
			p {
				color: #555;
				.p-header {
					font-weight: bold;
					color: #ffc200;
				}
			}
		}
	}
	
	.wrapper-middle-to-be-updated-motion {
		height: 100%;
		display: grid;
		grid-template-columns: 80% 20%;
		grid-column-gap: 18%;
		margin: 1.25% 12% 0 26%;
		// padding: 1.6% 0 3.3% 0;
		padding: 2% 0% 0% 0%;
		h1.how-it-started {
			font-size: 2.6em;
			font-weight: bold;
			color: #ffc200;
			// margin-left: 0%;
			// margin-top: 0;
		};
		p.thick {
			line-height: 1.5em;
			font-size: 0.9em;
			margin-bottom: 0;
			text-align: center;
		}
		.feedback {
			margin: auto;
			p {
				color: #555;
				.p-header {
					font-weight: bold;
					color: #ffc200;
				}
			}
		}
	}
	
	.wrapper-down {
		height: 100%;
		display: grid;
		grid-template-columns: 22.5% 22.5% 22.5% 22.5%;
		/* column-gap: 3%; */
		background:	#555;
		color: #fff;
		padding: 1.0% 13% 3.5% 26%;
		border-top: 7px solid #ffc200;
		/* margin-left: auto;
		margin-right: auto; */
		/* padding: 0% 0% 16% 28%; */
		/* margin-top: 15px; */
	}
	
	.wrapper-icons {
		display: grid;
		grid-template-columns: 50% 50%;
		/* grid-column-gap: 1%; */
		/* margin-left: -35%; */
	}
	
	p {
		font-size: 0.9em;
	}
	
	p.thin {
		text-align: left;
		line-height: 0.01em;
		font-size: 0.7em;
		.active {
			cursor: pointer;
			&:hover {
				color: #ffc200;
				text-decoration: underline;
			}
		}
	}
	
	p.thick {
		line-height: 1.4em;
		font-size: 0.9em;
	}
	
	p.thick-hidden {
		line-height: 0.39em;
		font-size: 0.9em;
		visibility: hidden;
	}
	
	p.thick-join-us {
		text-align: left;
		line-height: 2.0em;
		font-size: 0.7em;
	}
	
	.font-header {
		line-height: 1.2em;
		font-size: 1em;
		color: #ffc200;
		font-weight: bold;
		text-align: left;
		padding-top: 0.5em;
		padding-bottom: 0.7em;
	}
	
	.tooltip {
		max-width: 17.5%;
		font-weight: normal;
		line-height: 1.4em;
	}

	.button-deactivated {
		font-size: 1.35rem;
		/* padding: 5px 10px; */
		color:   #585858;
		text-align: center;
		margin: 0;
		padding: 0;
		.icon {
			cursor: pointer;
			opacity: .8;
			&:hover {
				filter: brightness(1.4);
			}
		}
		.icon-dislike {
			cursor: pointer;
			opacity: .8;
			transform: rotateY(-180deg);
			&:hover {
				filter: brightness(1.4);
			}
		}
		.digit {
			margin-left: 0.4vw;
			cursor: auto;
			font-size: 0.9em;
			font-weight: bold;
			opacity: .8;
		}
	}
	
	.button-activated {
		font-size: 1.35rem;
		/* padding: 5px 10px; */
		color: #007bff; // #1565f0
		text-align: center;
		margin: 0;
		padding: 0;
		.icon {
			cursor: pointer;
			&:hover {
				filter: brightness(1.4);
			}
		}
		.icon-dislike {
			cursor: pointer;
			transform: rotateY(-180deg);
			&:hover {
				filter: brightness(1.4);
			}
		}
		.digit {
			margin-left: 0.4vw;
			cursor: auto;
			font-size: 0.9em;
			font-weight: bold;
		}
	}
	
	// img.resize-like {
	// 	width: 30px;
	// 	/* max-height: 30%; */
	// 	cursor: pointer;
	// 	margin: 0;
	// 	padding: 0;
	// 	&:active {
	// 		border-radius: 15px;
	// 		border: 3px solid #ffc200;
	// 	}
	// }
	
	// img.resize-dislike {
	// 	max-width: 30px;
	// 	/* max-height: 30%; */
	// 	cursor: pointer;
	// 	transform: rotate(180deg);
	// 	margin: 0;
	// 	padding: 0;
	// 	&:active {
	// 		border-radius: 15px;
	// 		border: 3px solid #ffc200;
	// 	}
	// }
}

