.sidebar {
  position: fixed;
  top: 0;
  left: -150px;
  width: 150px;
  height: 222px;
  background-color: #555;
  transition: left 300ms ease-out;
}
.sidebar.active {
  left: 0;
}

.sidebar ul {
  margin-top: 10%;
  margin-left: 13%;
  padding: 0;
  text-align: left;
}
.sidebar li {
  list-style: none;  
}
.sidebar li a {
  text-decoration: none;
  padding: 12px;
  display: block;
  color: #fff;
  &:hover {
    color: #ffc200;
  }
  .nav-icon {
    max-width: 1vw;
  }
  .nav-headers {
    // display: inline-block;
    text-align: left;
  }
}


.sandwich {
  border: none;
  // border: 2px solid #888;
  border-radius: 7px;
  outline: 0;
  width: 50px;
  position: absolute;
  right: -50px;
  background-color: #ffc200;
  cursor: pointer;
  &:hover {
    // background-color: #ffb200;
    filter: brightness(1.1);
  }
}

button.sandwich:focus {
  outline: 1px none; 
}

.sandwich:after,
.sandwich:before,
.sandwich div {
  background-color: #666;
  height: 4px;
  margin: 8px 0;
  border-radius: 3px;
  border: none;
  content: "";
  display: block;
  transition: all 300ms ease-in-out;
}

.sidebar.active .sandwich:before {
  transform: translateY(12px) rotate(135deg);
}
.sidebar.active .sandwich::after {
  transform: translateY(-12px) rotate(-135deg);
}
.sidebar.active .sandwich div {
  transform: scale(0);
}
